<template>
	<div style="margin-top: 30px;">
		<div id="container"></div>
	</div>
</template>

<script>
	import {
		Chart
	} from '@antv/g2';
	export default {
		data() {
			return {
				dataSource: [],
			};
		},
		methods: {
			async getUserAgeData() {
				let {
					data
				} = await this.$get(this.$store.state.requestPath + '/admin/user_age/chart');
				this.dataSource = data.data;
			},
		},
		async created() {
			this.getUserAgeData();
		},
		watch: {
			dataSource: async function(obj) {
				const chart = new Chart({
				  container: 'container',
				  autoFit: true,
				  height: 500,
				  padding: [50, 20, 50, 20],
				});
				chart.data(obj);
				chart.scale('value', {
				  alias: '人数',
				});
				
				chart.axis('type', {
				  tickLine: {
				    alignTick: false,
				  },
				});
				chart.axis('value', false);
				
				chart.tooltip({
				  showMarkers: false,
				});
				chart.interval().position('type*value');
				chart.interaction('element-active');
				
				// 添加文本标注
				obj.forEach((item) => {
				  chart
				    .annotation()
				    .text({
				      position: [item.type, item.value],
				      content: item.value,
				      style: {
				        textAlign: 'center',
				      },
				      offsetY: -30,
				    })
				    .text({
				      position: [item.type, item.value],
				      content: (item.percent * 100).toFixed(0) + '%',
				      style: {
				        textAlign: 'center',
				      },
				      offsetY: -12,
				    });
				});
				chart.render();
			}
		},

	}
</script>

